import React, {useState} from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/swiper-bundle.css';
import { Link } from "react-router-dom"


const SliderOne = () => {

    const [swiper, setSwiper] = useState(null);

    const goNext = () => {
        if (swiper !== null) {
          swiper.slideNext();
        }
    };

    const goPrev = () => {
        if (swiper !== null) {
          swiper.slidePrev();
        }
    };


    return (
        <div className="banner-wrapper">
            <section className="banner-one banner-carousel__one no-dots">

                <Swiper getSwiper={setSwiper}>
                    <div className="banner-one__slide banner-one__slide-one">
                        <div className="container">
                            <div className="banner-one__bubble-1"></div>
                            <div className="banner-one__bubble-2"></div>
                            <div className="banner-one__bubble-3"></div>
                            <img src="/assets/images/slider-1-scratch.png" alt="" className="banner-one__scratch" />
                            <img src="/assets/images/slider-1-person-1.png" className="banner-one__person" alt="" />
                            <div className="row no-gutters">
                                <div className="col-xl-12">
                                    <h3 className="banner-one__title banner-one__light-color">Manten tu <br />
                                        Contenido Fresco</h3>
                                    <p className="banner-one__tag-line">El stock de contenido latino ms fresco</p>
                                    <Link to="/business" className="thm-btn banner-one__btn">Conoce ms</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </Swiper>
            </section>
            
            <div className="banner-one__cta">
                
                <div className="banner-one__cta-title">
                    <h3 className="banner-one__cta-text"><Link to="#">Tenemos cientos de imagenes y videos adecuados para ti</Link></h3>
                </div>
                <div className="banner-one__cta-link">
                    <Link to="#"><i className="kipso-icon-right-arrow"></i></Link>
                </div>
            </div>
        </div>
    );
}
export default SliderOne;